import React, { useState } from "react";
import { DatePicker } from "@material-ui/pickers";
import DateFnsUtils from '@date-io/date-fns';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import { makeStyles } from '@material-ui/core/styles';
import LiveTvIcon from '@material-ui/icons/LiveTv';
import DateRangeIcon from '@material-ui/icons/DateRange';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';

const useStyles = makeStyles((theme) => ({
  root: {
    boxSizing: 'border-box',
    position: 'relative',
    display: 'flex',
    justifyContent: 'space-between',
    width: '100%',
    padding: '12px 20px',
    borderBottom: '1px solid #ccc',
    '& .date': {
      textAlign: 'center',
      '& .week': {
        fontSize: '14px',
      },
      '& .month': {
        fontSize: '10px',
      },
      '&.now': {
        fontWeight: 'bold',
      },
    }
  },
  down: {
    marginLeft: '-6px',
  },
  hiddenDate: {
    opacity: 0,
    position: 'absolute',
    right: 20,
    width: 40,
  }
}));

const StaticDatePicker = () => {
  const classes = useStyles();

  const [selectedDate, handleDateChange] = useState(new Date());

  const [activeDate, activeDateChange] = useState(0);

  const wantedDate = (now, during) => {
    if(during === 0) {
      return `${now.getDate()} ${now.toLocaleString('en-us',{month: 'short'})}`
    } else {
      let paraStamp = now.getTime() + during * 24 * 60 * 60 * 1000;
      let paraDate = new Date(paraStamp);
      return `${paraDate.getDate()} ${paraDate.toLocaleString('en-us',{month: 'short'})}`
    }
  }

  const wantedWeek = (now, during) => {
    const weeks = ['SUN', 'MON', 'TUE', 'WEN', 'THU', 'FRI', 'SAT'];
    let paraStamp = now.getTime() + during * 24 * 60 * 60 * 1000;
    let paraDate = new Date(paraStamp);
    if(during === 0) {
      let nowDate = new Date();
      if(nowDate.toLocaleDateString() === paraDate.toLocaleDateString()) {
        return 'TODAY'
      } else {
        return `${weeks[paraDate.getDay()]}`
      }
    } else {
      return `${weeks[paraDate.getDay()]}`
    }
  }
  
  return (
    <div className={classes.root}>
      <LiveTvIcon />
      <div className={activeDate === -2 ? 'date now' : 'date'} onClick={() => activeDateChange(-2)}>
        <div className='week'>{wantedWeek(selectedDate, -2)}</div>
        <div className='month'>{wantedDate(selectedDate, -2)}</div>
      </div>
      <div className={activeDate === -1 ? 'date now' : 'date'} onClick={() => activeDateChange(-1)}>
        <div className='week'>{wantedWeek(selectedDate, -1)}</div>
        <div className='month'>{wantedDate(selectedDate, -1)}</div>
      </div>
      <div className={activeDate === 0 ? 'date now' : 'date'} onClick={() => activeDateChange(0)}>
        <div className='week'>{wantedWeek(selectedDate, 0)}</div>
        <div className='month'>{wantedDate(selectedDate, 0)}</div>
      </div>
      <div className={activeDate === 1 ? 'date now' : 'date'} onClick={() => activeDateChange(1)}>
        <div className='week'>{wantedWeek(selectedDate, 1)}</div>
        <div className='month'>{wantedDate(selectedDate, 1)}</div>
      </div>
      <div className={activeDate === 2 ? 'date now' : 'date'} onClick={() => activeDateChange(2)}>
        <div className='week'>{wantedWeek(selectedDate, 2)}</div>
        <div className='month'>{wantedDate(selectedDate, 2)}</div>
      </div>
      <div>
        <DateRangeIcon />
        <ArrowDropDownIcon className={classes.down} />
      </div>
      <div className={classes.hiddenDate}>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
          <DatePicker
            autoOk={true}
            disableToolbar
            variant="inline"
            format="MM/dd/yyyy"
            value={selectedDate}
            onChange={handleDateChange}
          />
        </MuiPickersUtilsProvider>
      </div>
    </div>
  );
};

export default StaticDatePicker;
