import React from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import HomeIcon from '@material-ui/icons/Home';
// import AccountCircleIcon from '@material-ui/icons/AccountCircle';
// import SyncIcon from '@material-ui/icons/Sync';
import SettingsInputSvideoIcon from '@material-ui/icons/SettingsInputSvideo';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import ChatIcon from '@material-ui/icons/Chat';
import SettingsIcon from '@material-ui/icons/Settings';
import { actionCreators } from './store';
import { actionCreators as headerActionCreators } from '../header/store';
import { actionCreators as switchActionCreators } from '../switch/store';

const useStyles = makeStyles({
  root: {
    position: 'fixed',
    left: 0,
    right: 0,
    bottom: 0,
    width: '100%',
  }
});

function Footer(props) {
  const classes = useStyles();

  const goto = (event, newValue) => {
    console.log('props111--', props)
    // console.log('event--', event)
    // console.log('newValue--', newValue)
    
    props.changeNav(newValue)
    
    switch(newValue) {
      case '/':
        props.history.push({pathname: newValue});
        props.changeTitle('Scores');
        break;
      case '/news':
        props.history.push({pathname: newValue});
        props.changeTitle('News');
        break;
      case '/chatroom':
        props.history.push({pathname: newValue});
        props.changeTitle('Chatroom');
        break;
      case '/switch':
        props.changeSwitch(!props.switchStatus);
        break;
      case '/settings':
        props.history.push({pathname: newValue});
        props.changeTitle('Settings');
        break;
      default:
        break;
    }
  }

  return (
    <BottomNavigation
      value={props.nav}
      onChange={(event, newValue) => goto(event, newValue)}
      showLabels
      className={classes.root}
    >
      <BottomNavigationAction value='/' label="Scores" icon={<HomeIcon />} />
      <BottomNavigationAction value='/news' label="News" icon={<LibraryBooksIcon />} />
      <BottomNavigationAction value='/switch' label='Switch' icon={<SettingsInputSvideoIcon />} />
      <BottomNavigationAction value='/chatroom' label="Chatroom" icon={<ChatIcon />} />
      <BottomNavigationAction value='/settings' label="Settings" icon={<SettingsIcon />} />
    </BottomNavigation>
  );
}

const mapStateToProps = (state, ownProps) => ({
  nav: state.footer.activeNav,
  switchStatus: state.switch.switchStatus
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  changeNav: (nav) => {
    dispatch(actionCreators.changeNav(nav))
  },
  changeTitle: (title) => {
    dispatch(headerActionCreators.changeTitle(title))
  },
  changeSwitch: (value) => {
    dispatch(switchActionCreators.changeSwitch(value))
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Footer));
