import * as constants from './constants';

const defaultState = {
	activeNav: localStorage.getItem('activeNav') ? localStorage.getItem('activeNav') : '/',
  // activeTitle: localStorage.getItem('Scores') ? localStorage.getItem('Scores') : 'Scores'
	// activeTitle: 'Scores'
};

//eslint-disable-next-line
export default (state = defaultState, action) => {
	switch(action.type) {
		case constants.CHANGE_NAV:
			// return state.set('activeNav', action.nav);
			localStorage.setItem('activeNav', action.nav);
      return Object.assign({}, state, {
        activeNav: action.nav,
        // activeTitle: action.title
      });
		default:
			return state;
	}
}
