import * as constants from './constants';

const defaultState = {
	loginStatus: localStorage.getItem('loginStatus') ? localStorage.getItem('loginStatus') : false,
	title: localStorage.getItem('activeTitle') ? localStorage.getItem('activeTitle') : 'Scores'
};

//eslint-disable-next-line
export default (state = defaultState, action) => {
	switch(action.type) {
		case constants.LOGIN_IN:
			localStorage.setItem('loginStatus', true);
      return Object.assign({}, state, {
        loginStatus: true,
      });
		case constants.LOGIN_OUT:
				localStorage.clear();
				return Object.assign({}, state, {
					loginStatus: false,
				});
		case constants.CHANGE_TITLE:
			localStorage.setItem('activeTitle', action.title);
			return Object.assign({}, state, {
				title: action.title,
			});
		default:
			return state;
	}
}
