import React from 'react';
import { connect } from 'react-redux';
// import Switch from '@material-ui/core/Switch';
import Paper from '@material-ui/core/Paper';
import Zoom from '@material-ui/core/Zoom';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
import { makeStyles } from '@material-ui/core/styles';
import { actionCreators } from './store';
import SportsSoccerIcon from '@material-ui/icons/SportsSoccer';
import SportsBasketballIcon from '@material-ui/icons/SportsBasketball';
import SportsKabaddiIcon from '@material-ui/icons/SportsKabaddi';

const useStyles = makeStyles((theme) => ({
  root: {
    // display: 'flex',
    // alignContent: 'center',
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    height: '100%',
    width: '100%',
    zIndex: 9999,
    backgroundColor: 'rgba(255,255,255,.4)',
  },
  container: {
    display: 'flex',
    alignItems: 'center',
    alignContent: 'center',
    justifyContent: 'center',
    flexWrap: 'wrap',
    height: '100%'
  },
  paper: {
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    margin: theme.spacing(1),
    height: 60,
    width: 60,
    textAlign: 'center',
    '& .name': {
      fontSize: '8px'
    }
  },
  svg: {
    width: 100,
    height: 100,
  },
  polygon: {
    fill: theme.palette.common.white,
    stroke: theme.palette.divider,
    strokeWidth: 1,
  },
}));

function SwitchBtn(props) {
  const classes = useStyles();
  // const [checked, setChecked] = React.useState(false);

  // const handleChange = () => {
  //   setChecked((prev) => !prev);
  // };

  const handleClick = () => {
    props.changeSwitch(!props.switchStatus);
  }

  const handleClickSports = (sports) => {
    console.log('sports--', sports)
  }

  return (
    <div className={classes.root} onClick={() => handleClick()}>
      {/* <FormControlLabel
        control={<Switch checked={checked} onChange={handleChange} />}
        label="Show"
      /> */}
      <div className={classes.container}>
        <Zoom in={props.switchStatus} onClick={() => handleClickSports('aa')}>
          <Paper elevation={4} className={classes.paper}>
            <div className='icon'><SportsSoccerIcon /></div>
            <div className='name'>FOOTERBALL</div>
            {/* <svg className={classes.svg}>
              <polygon points="0,100 50,00, 100,100" className={classes.polygon} />
            </svg> */}
          </Paper>
        </Zoom>
        <Zoom in={props.switchStatus} onClick={() => handleClickSports('bb')} style={{ transitionDelay: props.switchStatus ? '500ms' : '0ms' }}>
          <Paper elevation={4} className={classes.paper}>
            <div className='icon'><SportsBasketballIcon /></div>
            <div className='name'>BASKETBALL</div>
            {/* <svg className={classes.svg}>
              <polygon points="0,100 50,00, 100,100" className={classes.polygon} />
            </svg> */}
          </Paper>
        </Zoom>
        <Zoom in={props.switchStatus} onClick={() => handleClickSports('bb')} style={{ transitionDelay: props.switchStatus ? '1000ms' : '0ms' }}>
          <Paper elevation={4} className={classes.paper}>
            <div className='icon'><SportsKabaddiIcon /></div>
            <div className='name'>BOXING</div>
            {/* <svg className={classes.svg}>
              <polygon points="0,100 50,00, 100,100" className={classes.polygon} />
            </svg> */}
          </Paper>
        </Zoom>
      </div>
    </div>
  );
}

const mapStateToProps = (state, ownProps) => ({
  switchStatus: state.switch.switchStatus
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  changeSwitch: (value) => {
    dispatch(actionCreators.changeSwitch(value))
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(SwitchBtn);
