import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import LockOpenIcon from '@material-ui/icons/LockOpen';
import InfoIcon from '@material-ui/icons/Info';
import AssignmentIcon from '@material-ui/icons/Assignment';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
// import HelpIcon from '@material-ui/icons/Help';
// import QuestionAnswerIcon from '@material-ui/icons/QuestionAnswer';
import { actionCreators as HeadererActionCreators} from '../../components/header/store';
import Snackbar from '@material-ui/core/Snackbar';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    padding: 0,
    backgroundColor: theme.palette.background.paper,
    borderTop: '1px solid #ddd',
  },
  eachList: {
    borderBottom: '1px solid #ddd',
  }
}));

function NestedList(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };

  const Logout = () => {
    props.Logout();
    props.history.push({pathname: '/'});
  };

  const closeTip = () => {
    setOpen(false);
  };

  return (
    <Fragment>
      <Snackbar
        anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
        open={open}
        onClose={closeTip}
        message={"Haven't been finished"}
      />
      <List
        component="nav"
        className={classes.root}
      >
        <ListItem button className={classes.eachList} onClick={handleClick}>
          <ListItemIcon>
            <LockOpenIcon />
          </ListItemIcon>
          <ListItemText primary="Change password" />
          <ChevronRightIcon />
        </ListItem>
        <ListItem button className={classes.eachList} onClick={handleClick}>
          <ListItemIcon>
            <InfoIcon />
          </ListItemIcon>
          <ListItemText primary="About" />
          <ChevronRightIcon />
        </ListItem>
        <ListItem button className={classes.eachList} onClick={handleClick}>
          <ListItemIcon>
            <AssignmentIcon />
          </ListItemIcon>
          <ListItemText primary="Privacy" />
          <ChevronRightIcon />
        </ListItem>
        {/* <ListItem button className={classes.eachList} onClick={handleClick}>
          <ListItemIcon>
            <HelpIcon />
          </ListItemIcon>
          <ListItemText primary="Help" />
          <ChevronRightIcon />
        </ListItem>
        <ListItem button className={classes.eachList} onClick={handleClick}>
          <ListItemIcon>
            <QuestionAnswerIcon />
          </ListItemIcon>
          <ListItemText primary="FAQs" />
          <ChevronRightIcon />
        </ListItem> */}
        <ListItem button onClick={Logout}>
          <ListItemIcon>
            <ExitToAppIcon />
          </ListItemIcon>
          <ListItemText primary="Logout" />
          <ChevronRightIcon />
        </ListItem>
      </List>
    </Fragment>
  );
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  Logout: () => {
    dispatch(HeadererActionCreators.logout())
  }
})

export default connect(null, mapDispatchToProps)(withRouter(NestedList));
