import React, { Fragment, useState } from 'react';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
// import MenuIcon from '@material-ui/icons/Menu';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
// import Menu from '@material-ui/core/Menu';
// import MenuItem from '@material-ui/core/MenuItem';
// import Fade from '@material-ui/core/Fade';
import Avatar from '@material-ui/core/Avatar';
import { deepOrange } from '@material-ui/core/colors';
import { withRouter } from 'react-router-dom';
import { actionCreators } from './store';
import { actionCreators as FooterActionCreators} from '../footer/store';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    paddingLeft: '8px'
  },
  orange: {
    color: theme.palette.getContrastText(deepOrange[500]),
    backgroundColor: deepOrange[500],
  },
  menu: {
    top: '43px !important'
  }
}));

function Header(props) {
  const classes = useStyles();
  // console.log('props--header--', props)

  const [anchorEl, setAnchorEl] = useState(null);
  const open = Boolean(anchorEl);
  console.log('anchorEl--', anchorEl)
  console.log('open--', open)
  
  const handleClick = (event) => {
    // console.log('event--', event.currentTarget)
    props.changeTitle('Profile');
    props.changeNav('/profile')
    props.history.push({pathname: '/profile'});
    setAnchorEl(event.currentTarget);
  };

  const handleLogin = (event) => {
    props.history.push({pathname: '/login'});
  };

  // const handleLogout = () => {
  //   setAnchorEl(null);
  //   props.history.push({pathname: '/login'});
  // };

  // const GotoProfile = () => {
  //   setAnchorEl(null);
  //   props.history.push({pathname: '/profile'});
  // };

  // const handleClose = () => {
  //   setAnchorEl(null);
  // };

  return (
    <div className={classes.root}>
      <AppBar position="fixed">
        <Toolbar>
          {/* <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton> */}
          <Avatar className={classes.orange}>LOGO</Avatar>
          <Typography variant="h6" className={classes.title}>
            {props.title}
          </Typography>
          {
            props.loginStatus ?
              <Fragment>
                <IconButton aria-label="account" aria-controls="fade-menu" aria-haspopup="true" onClick={handleClick}>
                  <AccountCircleIcon fontSize="large" />
                </IconButton>
                {/* <Menu
                  id="fade-menu"
                  className={classes.menu}
                  anchorEl={anchorEl}
                  keepMounted
                  open={open}
                  onClose={handleClose}
                  TransitionComponent={Fade}
                >
                  <MenuItem onClick={() => GotoProfile()}>Profile</MenuItem>
                  <MenuItem onClick={() => handleLogout()}>Logout</MenuItem>
                </Menu> */}
              </Fragment> :
              <Button  onClick={handleLogin} color="inherit">Login</Button>
          }
          
        </Toolbar>
      </AppBar>
    </div>
  );
}

const mapStateToProps = (state, ownProps) => ({
  loginStatus: state.header.loginStatus,
  title: state.header.title
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  changeNav: (nav) => {
    dispatch(FooterActionCreators.changeNav(nav))
  },
  changeTitle: (title) => {
    dispatch(actionCreators.changeTitle(title))
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header));
