import React from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter, Route } from 'react-router-dom';
import store from './store';
import Scores from './pages/scores';
import ScoreDetail from './pages/scoreDetail';
import News from './pages/news';
import NewsDetail from './pages/newsDetail';
import Chatroom from './pages/chatroom';
import Profile from './pages/profile';
import Settings from './pages/settings';
import Login from './pages/login';
import Register from './pages/register';

function App() {
  return (
    <Provider store={store}>
      <BrowserRouter>
        <Route path='/' exact component={Scores}></Route>
        <Route path='/score-detail' exact component={ScoreDetail}></Route>
        <Route path='/login' exact component={Login}></Route>
        <Route path='/register' exact component={Register}></Route>
        <Route path='/news' exact component={News}></Route>
        <Route path='/settings' exact component={Settings}></Route>
        <Route path='/news-detail' exact component={NewsDetail}></Route>
        <Route path='/chatroom' exact component={Chatroom}></Route>
        <Route path='/profile' exact component={Profile}></Route>
      </BrowserRouter>
    </Provider>
  );
}

export default App;
