import React, { Component } from 'react'
import { connect } from 'react-redux';
import Header from '../header/header';
import Footer from '../footer/footer';
import Switch from '../switch/switch';
import './navLayouts.css';
class NavLayouts extends Component {
  // constructor(props) {
  //   super(props)
  // }
  render() {
    const { children } = this.props;
    return (
      <div className='page'>
        <Header />
        <div className='content'>
          {children}
          {this.props.switchStatus && <Switch />}
        </div> 
        <Footer />
      </div>
    );
  }
}

// export default NavLayouts;

const mapStateToProps = (state, ownProps) => ({
  switchStatus: state.switch.switchStatus
})

export default connect(mapStateToProps, null)(NavLayouts);
